@font-face {
  font-family: 'alegreya';
  src: url("../fonts/alegreyasanssc-black-webfont.woff2") format("woff2"), url("../fonts/alegreyasanssc-black-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html,
body {
  height: 100%;
  background-color: #fcf5e6;
}

body {
  font-size: 15px;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  color: #1b1b1b;
}

.wrapper {
  position: relative;
  width: 100%;
  min-width: 1440px;
  min-height: 650px;
  overflow: hidden;
}

.container {
  width: 1290px;
  margin: 0 auto;
}

.footer-container {
  max-width: 1100px;
}

.container-product {
  max-width: 1100px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.tabel {
  border-collapse: collapse;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

.section {
  height: 100vh;
  min-height: rem(650px);
}

.hero {
  background: url("../img/bg/hero.jpg") center center/cover no-repeat;
  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
}

.header__contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header__contact {
  font-size: 16px;
}

.phone__header {
  background-color: #212121;
  padding: 8px 37px;
  color: #ffbc2f;
}

.hero__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1140px;
  height: 72%;
}

.slider {
  height: 100%;
}

.slider__item {
  background: center center / contain no-repeat;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slider__item {
  opacity: 0;
  transition: opacity .9s;
}

.slider__item--active {
  opacity: 1;
}

.slider__item--price {
  background-image: url("../img/content/hero/slider_pic_1.png");
}

.slider__item--games {
  background-image: url("../img/content/hero/slider_pic_2.png");
}

.slider__item--present {
  background-image: url("../img/content/hero/slider_pic_3.png");
}

.slider__item--games .slider-text {
  white-space: nowrap;
  top: 88%;
  left: 119%;
  align-items: center;
}

.slider__item--present .slider-text {
  white-space: nowrap;
  top: 78%;
  left: 65%;
  align-items: center;
}

.slider-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-150%, -52%);
  font-family: 'alegreya';
  color: #fff;
  font-size: 63px;
  text-transform: uppercase;
  line-height: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.slider-text__word {
  background: #000;
  padding: 0 17px 8px;
  margin-top: -7px;
}

.slider-text__word:nth-child(2n) {
  transform: translateX(10px);
}

.slider-text__word--yellow {
  color: #ffc92f;
}

.switch__list {
  display: flex;
  font-family: "Yanone Kaffeesatz";
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  justify-content: center;
  margin-top: 130px;
  margin-bottom: 104px;
}

.switch__item {
  margin-right: 65px;
}

.switch__item:last-child {
  margin-right: 0;
}

.switch__link {
  padding: 6px 18px;
}

.switch__link:hover {
  color: #d82727;
}

.switch__item--active .switch__link {
  color: #ffc92f;
  background-color: #1b1b1b;
}

.products__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 180px;
  margin-left: -27px;
}

.products__item {
  width: 302px;
  height: 386px;
  background-color: #ffc92f;
  margin-bottom: 35px;
  margin-left: 27px;
  position: relative;
}

.products__item--color {
  background-color: #232323;
}

.products__item--color .products__price {
  border-bottom-color: #ffc92f;
}

.products__item--size {
  width: 630px;
  height: 544px;
}

.products__item--size .products__price-amount {
  color: #212121;
}

.products-img {
  width: 90%;
}

.products-content {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.products-content:hover .products__desc {
  transform: translateY(0%);
}

.products__price {
  width: 124px;
  border-bottom: 47px solid #000;
  position: absolute;
  top: 18px;
  left: -7px;
  border-left: 6px solid transparent;
  border-right: 3px solid transparent;
  transform: rotate(-181deg);
}

.products__price-amount {
  transform: rotate(181deg);
  position: absolute;
  color: #fcf5e6;
  font-family: "Yanone Kaffeesatz";
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  left: 30%;
}

.products__desc {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 23px 30px;
  text-align: center;
  background-color: rgba(35, 35, 35, 0.8);
  transform: translateY(100%);
  transition: .4s;
}

.products-text {
  color: #fcf5e6;
  margin-bottom: 15px;
}

.btn {
  padding: 5px 27px;
  background-color: #1b1b1b;
  font-family: "Yanone Kaffeesatz";
  font-size: 24px;
  font-weight: 700;
  color: #ffc92f;
  text-transform: uppercase;
  display: inline-block;
}

.btn:hover {
  background: #d82727;
  color: #fcf5e6;
}

.collection__list {
  display: flex;
  overflow: hidden;
  margin-bottom: 140px;
}

.collection__item {
  flex: 1;
  margin-right: 24px;
  min-height: 473px;
  background: url("../img/bg/pattern.png") center center/500px;
  overflow: hidden;
  position: relative;
}

.collection__item:last-child {
  margin-right: 0;
}

.collection-desc {
  display: block;
  padding: 75px 8% 75px 44%;
  height: 100%;
}

.collection-desc:hover .btn {
  background: #d82727;
  color: #fcf5e6;
}

.collection-title {
  font-family: "Yanone Kaffeesatz";
  font-size: 70px;
  font-weight: 700;
  line-height: 1;
  margin: 0 auto;
  text-transform: uppercase;
  letter-spacing: 3.5px;
  margin-bottom: 10px;
}

.collection-text {
  margin-bottom: 25px;
}

.collection__item::before {
  content: "";
  display: block;
  background: center center / contain no-repeat;
  position: absolute;
  height: 180%;
  width: 60%;
  top: -17%;
  left: -16%;
}

.collection__item--man::before {
  background-image: url("../img/collections/collection_man.png");
}

.collection__item--woman::before {
  background-image: url("../img/collections/collection_woman.png");
  top: -44%;
}

.menu {
  margin-bottom: 122px;
}

.menu__section {
  display: flex;
  justify-content: space-between;
}

.menu__col {
  margin-right: 20px;
}

.menu__col:last-child {
  margin-right: 0;
}

.menu__title {
  font-family: "Yanone Kaffeesatz";
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
  margin-bottom: 28px;
}

.menu__item {
  color: #737373;
  margin-bottom: 17px;
  line-height: 1;
}

.menu__item:hover {
  color: #d82727;
}

.menu__contacts {
  margin-bottom: 30px;
}

.menu__contacts:last-child {
  margin-bottom: 0;
}

.menu__contacts-link {
  display: block;
  margin-bottom: 8px;
}

.extra {
  display: flex;
  margin-bottom: 97px;
}

.extra__col {
  flex: 1;
  margin-right: 26px;
}

.extra__col:last-child {
  margin-right: 0;
}

.extra__best {
  display: block;
  padding: 50px 130px;
  font-family: "Yanone Kaffeesatz";
  color: #ffffff;
  font-size: 50px;
  font-weight: 700;
  line-height: 48px;
  text-transform: uppercase;
  background-color: #232323;
  text-align: center;
}

.extra__col-socials {
  height: 100%;
  background: url("../img/bg/pattern.png") center center/500px;
}

.socials__list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.socials__item {
  margin-right: 46px;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: block;
  width: 38px;
  height: 38px;
}

.socials__link:hover {
  fill: #d82727;
}

.socials__icon {
  width: 100%;
  height: 100%;
}

.sidebar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 75px;
  background-color: #212121;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 auto;
  transition: width .5s;
}

.sidebar--open {
  width: 325px;
  padding: 0 3% 0 4%;
}

.sidebar--open .sidebar__header {
  display: flex;
}

.sidebar--open .sidebar-menu {
  left: 0;
  width: 100%;
  text-align: center;
  transition-delay: .1s;
}

.sidebar-menu {
  position: absolute;
  left: -500px;
  transition: left .4s;
}

.sidebar__header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: none;
  margin-top: 30px;
  justify-content: center;
}

.sidebar__close {
  display: block;
  width: 21px;
  height: 21px;
  fill: #ffc92f;
  position: absolute;
  right: 30px;
  top: 0px;
}

.close-img {
  width: 100%;
  height: 100%;
}

.sidebar__list {
  text-align: center;
  font-family: "Yanone Kaffeesatz";
  color: #ffc92f;
  font-size: 20px;
  text-transform: uppercase;
}

.sidebar__item {
  margin-bottom: 20px;
}

.sidebar__item:hover {
  color: #d82727;
}

.hamburger {
  display: block;
  width: 23px;
  height: 23px;
  position: absolute;
  fill: #ffc92f;
  top: 30px;
  left: 50%;
  transform: translate(-50%);
}

.hamburger-img {
  width: 100%;
  height: 100%;
}

.sidebar--open .hamburger {
  display: none;
}

.header-product {
  background: url("../img/bg/hero.jpg") center center/cover no-repeat;
  display: flex;
  padding: 31px 0;
  justify-content: space-around;
  align-items: center;
  position: relative;
  overflow: hidden;
  margin-bottom: 90px;
}

.header__icon {
  width: 150px;
  height: 119px;
}

.header__col {
  position: relative;
}

.header-product::before {
  content: "";
  display: block;
  background: url("../img/decor/header-guys.png") center center/contain no-repeat;
  position: absolute;
  width: 33%;
  height: 157%;
  top: -15%;
}

.header__text {
  font-family: 'alegreya';
  font-size: 31px;
  color: #ffffff;
  line-height: 1.1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-transform: uppercase;
  position: absolute;
  right: 79px;
  top: -67px;
}

.product {
  margin-bottom: 95px;
}

.product__content {
  display: flex;
  background-color: #ffc92f;
  min-height: 668px;
}

.product__img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48%;
}

.product-shirt {
  width: 90%;
}

.product__desc {
  background-color: #fcf5e6;
  flex: 1;
  margin: 1%;
  display: flex;
  flex-direction: column;
  padding: 24px 30px;
}

.product__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  border-bottom: 1px solid #e0d9c8;
}

.product__title-name {
  font-size: 17px;
  font-weight: 700;
  color: #ffc92f;
  line-height: 22.56px;
  margin-bottom: 8px;
}

.product__title-desc {
  font-family: "Yanone Kaffeesatz";
  font-size: 47px;
  font-weight: 700;
  letter-spacing: 2.5px;
  line-height: 47px;
  margin-bottom: 10px;
}

.product__title-price {
  font-family: "Yanone Kaffeesatz";
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1% 6%;
  background-color: #ffc92f;
  margin-bottom: 29px;
  line-height: 1;
}

.product__text {
  margin-bottom: 25px;
  border-bottom: 1px solid #e0d9c8;
}

.product__list {
  font-size: 13px;
  margin-top: 32px;
  margin-bottom: 42px;
}

.product__item {
  margin-bottom: 13px;
}

.product__item:last-child {
  margin-bottom: 0;
}

.product__controls {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.product__sizes {
  margin-bottom: 29px;
}

.product__sizes-list {
  display: flex;
}

.product__sizes-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
}

.product__sizes-item {
  border: 2px solid #e0d9c8;
  margin-right: 7px;
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
}

.product__sizes-item--lock {
  color: #cac3b3;
}

.product__sizes-item--active {
  background-color: #ffc92f;
  border-color: #ffc92f;
}

.product__sizes-item--unlock:hover {
  border-color: #ffc92f;
}

.product__color {
  margin-bottom: 35px;
}

.product__color-list {
  display: flex;
}

.product__color-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 15px;
}

.product__color-item:hover {
  border: 2px solid #ffc92f;
}

.product__color-item--active {
  border: 2px solid #ffc92f;
}

.product__color-link {
  width: 22px;
  height: 22px;
  display: block;
  border-radius: 50%;
}

.product__color-link--black {
  background-color: #2f2c25;
}

.product__color-link--grey {
  background-color: #e0d9c8;
}

.product__color-link--green {
  background-color: #529029;
}

.product__color-link--blue {
  background-color: #39498d;
}

.product__color-link--red {
  background-color: #d82727;
}

.btn--product {
  padding: 16px 75px;
  font-size: 28px;
  line-height: 23.51px;
  letter-spacing: 1.2px;
}

.img-shirt {
  padding: 25% 4%;
  max-width: 102%;
}

.description__list {
  position: relative;
  display: flex;
  flex-direction: column;
}

.description__item {
  display: flex;
  min-height: 298px;
  font-size: 13px;
  border: 10px solid #ffc92f;
}

.description__col-left {
  width: 32%;
  border-right: 2px solid #e5dece;
}

.description__col-right {
  width: 68%;
}

.description__washing {
